<template>
  <div id="app">
    <h1 style="text-align: center;">打工人日历</h1>
    <el-row class="range">
      <span>范围: </span>
      <el-date-picker
        v-model="daterange"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        @change="dateChange">
      </el-date-picker>
    </el-row>
    <el-row class="count">
      共{{ dateList.count }}天,&#129521;{{ dateList.workCount }}天,&#127796;{{ dateList.count-dateList.workCount }}天。
    </el-row>
    <el-row class="tips">
      今天是{{ today }}<br/>老子今天不上班！
    </el-row>
    <el-row>
      <el-col :span='27' :key="index"  v-for='(item,index) in ["一","二","三","四","五","六","日"]'>
        {{ "星期"+item }}
      </el-col>
    </el-row>
    <el-row>
      <el-col :span='27' :key="index"  v-for='(item,index) in dateList'>
      <el-card v-if="item.day" @click.native="changeState(index,item)">
        {{ item.day }}
        <span>{{ item.holiday?'&#127796;':'&#129521;' }}</span>
      </el-card>
    </el-col>
    </el-row>
  </div>
</template>

<script>


export default {
  name: 'App',
  created() {
    let date = new Date()
    this.stime = new Date(`${date.getFullYear()}-${date.getMonth()+1}-1`)
    let nextMonth = new Date(`${date.getFullYear()}-${date.getMonth()+2}-1`)
    this.etime = new Date(nextMonth.setDate(nextMonth.getDate()-1))

    this.daterange[0] = this.stime
    this.daterange[1] = this.etime

    this.axios.get("https://timor.tech/api/holiday/year/")
    .then(res=>{
      this.holiday = res.data
      this.getDateList()
    })
  },
  data() {
    return {
      daterange: [],
      stime: null,
      etime: null,
      dateList: [],
      holiday: null,
    }
  },
  methods: {
    getDateList() {
      let offset = this.stime.getDay()===0?6:this.stime.getDay()-1
      for(let i=0;i<offset;i++){
        this.dateList.push({})
      }

      let tempDate = new Date(this.stime)
      let count = 0
      let workCount = 0
      while (tempDate<=this.etime) {
        count = count + 1
        let month = tempDate.getMonth()+1<10?'0'+(tempDate.getMonth()+1):tempDate.getMonth()+1
        let day = tempDate.getDate()<10?'0'+tempDate.getDate():tempDate.getDate()
        let date = month+'-'+day
        let holiday = tempDate.getDay() === 6 || tempDate.getDay() === 0
        holiday = this.holiday.holiday[date]?this.holiday.holiday[date].holiday:holiday
        if (holiday === false){
          workCount = workCount + 1
        }
        this.dateList.push({
          day: date,
          holiday: holiday,
        })
        tempDate = new Date(tempDate.setDate(tempDate.getDate()+1))
      }
      this.dateList.count = count
      this.dateList.workCount = workCount
    
    },
    dateChange(){
      this.stime = this.daterange[0]
      this.etime = this.daterange[1]
      this.dateList = []
      this.getDateList()
    },
    changeState(index,item){
      item.holiday = !item.holiday
      if(item.holiday){
        this.dateList.workCount = this.dateList.workCount - 1
        this.$message.success(item.day.split('-')[0]+'月'+item.day.split('-')[1]+'日 '+'切换为休息状态')
      } else {
        this.dateList.workCount = this.dateList.workCount + 1
        this.$message.success(item.day.split('-')[0]+'月'+item.day.split('-')[1]+'日 '+'切换为搬砖状态')
      }
      this.dateList[index] = item
    }
  },
  computed: {
    today: function(){
      let date = new Date()
      let weeks = ['日','一','二','三','四','五','六']
      return date.getMonth()+1+'月'+date.getDate()+'日 '+'星期'+weeks[date.getDay()]
    }
  }
}
</script>

<style scoped>
  #app {
    position:absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    color: #fff;
    background-color: #000a;
    padding-top: 80px;
  }
  .el-row {
    padding: 0 100px;
  }
  .el-col{
    border: 1px solid transparent;
    height: 70px;
    text-align: center;
  }
  .el-col-27{
    width: 14%;
  }
  .el-card{
    margin: 3px;
  }
  .range, .count, .tips{
    text-align: center;
    margin: 20px;
  }
  .tips {
    color: #C0C4CC;
  }
</style>